import { Component } from 'react';
import Button from '../../button';
import message from '../index';

export default class MessageDemo extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    message.config({
      top: 60,
      duration: 10,
    });
    return (
      <div className="markdown-block">
        <h3></h3>
        <p></p>
        <Button
          onClick={() => {
            message.info('');
          }}
        >
          info
        </Button>&emsp;
        <Button
          type="secondary"
          onClick={() => {
            message.success('');
          }}
        >
          success
        </Button>&emsp;
        <Button
          type="secondary"
          onClick={() => {
            message.error('');
          }}
        >
          error
        </Button>&emsp;
        <Button
          type="secondary"
          onClick={() => {
            message.warning('');
          }}
        >
          warning
        </Button>
      </div>
    );
  }
}



